
import tools from '../../lib/tools.js';

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Spinner from 'vux/src/components/spinner/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'orderDetail',
components:{Spinner,XDialog,Toast},
directives: {TransferDom},
data(){
return {
id:'',
showToast:false,
toastMsg:'',
orderInfo:{},
address:{},
showDialogPay:false
}

},
created(){
this.loading();
},
methods:{
loading(){
var vm = this;
let id = tools.getRequestParam('id');
console.log(id)
this.$http.get('/orderInfo/getOrderDetailById?id='+id).then((result) => {
let data = result.data;
if(data.successful && (data.resultCode.code=='SUCCESS')){
vm.orderInfo = data.data;
let adress = data.data.userObject.address;
vm.address = adress ? JSON.parse(adress) : {name:'',mobile:'',address:'',detailedAddress:'',zipCode:''}
document.getElementById('describeText').innerHTML = vm.orderInfo.relationDesc;

const imgs = document.querySelectorAll('#describeText img')
for(var i=0;i<imgs.length;i++){
imgs[i].className = 'img-responsive';
imgs[i].style.display = 'block';
imgs[i].style.maxWidth = '100%';
imgs[i].style.height = 'auto';
}
if(vm.orderInfo.goodsList&&vm.orderInfo.goodsList.length>0){
document.querySelector('.serviceList').style.borderTop='1px solid #DEDEDE';
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data.resultCode);
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
document.querySelector('html').style.height = '100%';
document.body.style.height = '100%';
},
buyGood(orderCode){

console.log(this.price,this.goodId,this.type,this.id);
let vm = this;
vm.showDialogPay = true;
let uri = window.location.href.indexOf('serviceIndex.html')>=0?'/m/myService.html':'/m/myOrderList.html';

this.$http.axios({method:'get',url:'/api/WeiXin/wx_project_repeatpay.action?orderCode='+orderCode}).then((result) => {
var data = result.data || result;
console.log(JSON.stringify(result)+"#"+result.appId+"#");

function onBridgeReady(){
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId":data.appId,     //公众号名称，由商户传入
"timeStamp":data.timeStamp,         //时间戳，自1970年以来的秒数
"nonceStr":data.nonceStr, //随机串
"package":"prepay_id="+data.prepayId,
"signType":"MD5",         //微信签名方式：
"paySign":data.sign1 //微信签名
},
function(res){
if(res.err_msg == "get_brand_wcpay_request:ok" ) {

setTimeout(function(){
window.location.href = uri;
},2000);
}     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
}
);
}
console.log(data);
if(data.ret_code=="0"){
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
}
}else{
onBridgeReady();
}
}else if(data.ret_code=="3"){
vm.showToast = true;
vm.toastMsg = data.ret_msg;
setTimeout(function(){
window.location.href = uri;
},1500);
}else{
vm.showDialogPay = false;
vm.showAddress = false;
vm.showToast = true;
vm.toastMsg = data.ret_msg;
return false;
}
},(error) => {
vm.showDialogPay = false;
vm.showAddress = false;
console.log("我进error了！！");
vm.showToast = true;
vm.toastMsg = '支付失败';
console.error(error);
})
}
}
}



